<template>
	<div>
		<div class="DetailsNav">
			<i class="el-icon-back" @click="goBack"></i>
		</div>

		<div style="font-size: 14px; margin-bottom: 10px;">
			<span style="font-size: 15px; font-weight: bold">订单编号：</span>{{orderDetails.OrderNO}}
		</div>

		<div v-if="orderDetails.expressType" style="font-size: 14px; margin-bottom: 10px;">
			<span style="font-size: 15px; font-weight: bold">取货方式：</span>{{orderDetails.expressType}}
		</div>

		<div v-if="orderDetails.expressType" style="font-size: 14px; margin-bottom: 10px;">
			<span style="font-size: 15px; font-weight: bold">取货时间：</span>{{orderDetails.pickUpTime}}
		</div>

		<div style="font-size: 14px; margin-bottom: 10px;">
			<span style="font-size: 15px; font-weight: bold">地址：</span>{{orderDetails.address}}
		</div>

		<div style="font-size: 14px; margin-bottom: 10px;">
			<span style="font-size: 15px; font-weight: bold">备注信息：</span>{{orderDetails.remark}}
		</div>

		<Table
				border
				:columns="columns"
				:data="orderDetails.content"
				size="large"
		>
		</Table>

		<div class="go-to">
			<span style="font-size: 16px; margin-right: 30px">商品总量：{{totalCount}}</span>
			<span style="font-size: 16px;">商品总价：￥{{totalPrice}}</span>
		</div>
	</div>
</template>
<script>
	import {mapState} from 'vuex';

	export default {
		name: "MyorderDetails",

		data() {
			return {
				columns: [
					{
						title: "图片",
						key: "img",
						width: 80,
						render: (h, params) => {
							return h("div", [
								h("img", {
									attrs: {
										src: params.row.GoodsImg,
										width: 40,
										height: 40,
									}
								})
							]);
						},
						align: "center"
					},
					{
						type: 'html',
						title: "条码",
						width: 250,
						key: "SerialNumber",
						align: "center"
					},
					{
						title: "商品",
						key: "GoodsName",
						align: "center"
					},
					{
						title: "属性",
						width: 350,
						key: "specs",
						align: "center"
					},
					{
						title: "下单数量",
						width: 100,
						key: "GoodsCount",
						align: "center",
					},
					{
						title: "实发数量",
						width: 100,
						key: "DeliverCount",
						align: "center",
					},
					{
						title: "金额",
						width: 150,
						key: "price",
						align: "center"
					},
					{
						title: "小计",
						width: 150,
						key: "totalPrice",
						align: "center"
					},
				],
			}
		},

		computed: {
			...mapState(['orderDetails']),

			totalCount: {
				get() {
					let total = 0;
					this.orderDetails.content.forEach(item => {
						total += item.DeliverCount;
					});
					return total;
				}
			},

			totalPrice: {
				get() {
					let total = 0;
					this.orderDetails.content.forEach(item => {
						total += item.DeliverCount * item.SpecialOffer;
					});
					return total;
				}
			},
		},

		methods: {
			goBack() {
				this.$parent.hideOrderDetails();
			},
		}
	}
</script>
<style scoped>
	.el-icon-back {
		font-size: 25px;
		cursor: pointer;
	}

	.DetailsNav {
		padding: 15px 0;
	}

	.go-to {
		margin: 15px;
		display: flex;
		justify-content: flex-end;
	}
</style>


