<template>
	<div>
		<MyorderDetails v-if="isDetailsShow"></MyorderDetails>
		<div v-else>
			<div class="MyOrder_header">
				<div
						v-for="(item,index) in orders[state]"
						:key="index"
						:class="{'xz':index===switchxz}"
						@click="switchTab(index)"
				>{{item.content}}
				</div>
			</div>
			<Table border :columns="columns" :data="orderResult.content" size="large" no-data-text="你还没有订单，快点去购物吧"></Table>
			<div class="page-size">
				<Page :total="orderResult.accattach" :page-size="10" :current.sync="pageID" show-total @on-change="changePage"></Page>
			</div>
		</div>
	</div>
</template>
<script>
	import MyorderDetails from "./MyorderDetails.vue";
	import {mapState, mapActions} from 'vuex';

	export default {
		name: "myOrder",

		components: {
			MyorderDetails
		},

		data() {
			return {
				pageID: 1,
				isDetailsShow: false,
				columns: [
					{
						title: "订单号",
						key: "Out_Trade_No",
						width: 250,
						align: "center"
					},
					{
						title: "商品",
						key: "title",
						align: "center"
					},
					{
						title: "金额",
						width: 150,
						key: "Money",
						align: "center"
					},
					{
						title: "下单时间",
						width: 200,
						key: "CreateTime",
						align: "center"
					},
					{
						title: "订单类型",
						width: 150,
						key: "OrderType",
						align: "center"
					},
					{
						title: "操作",
						key: "readMore",
						align: "center",
						width: 200,
						render: (h, params) => {
							if (this.state === 0 && this.switchxz === 0) {
								return h('div', [
									h('Button', {
										props: {
											type: 'primary',
										},
										style: {
											marginRight: '5px'
										},
										on: {
											click: () => {
												this.showOrderDetails(params.row)
											}
										}
									}, '详情'),
									h('Button', {
										props: {
											type: 'error',
										},
										on: {
											click: () => {
												this.cancelOrder(params.row)
											}
										}
									}, '取消')
								]);
							} else {
								return h('div', [
									h('Button', {
										props: {
											type: 'primary',
										},
										style: {
											marginRight: '5px'
										},
										on: {
											click: () => {
												this.showOrderDetails(params.row)
											}
										}
									}, '详情')
								]);
							}
						}
					}
				],
				state: 0,
				orders: [
					// [{content: "未处理", status: 0}, {content: "已处理", status: 2}, {content: "待接单", status: 3}, {content: "已接单", status: 4}],
					[{content: "未处理", status: 0}, {content: "待接单", status: 3}, {content: "已接单", status: 4}],
					[{content: "已完成", status: 1}, {content: "已取消", status: 5}]
				],
				switchxz: 0,
			};
		},

		beforeRouteUpdate(to, from, next) {
			next();
			this.state = this.$route.params.state - 1;
			this.switchxz = 0;
			this.pageID = 1;
			this.isDetailsShow = false;
			this.loadOrderResult([this.pageID - 1, this.orders[this.state][this.switchxz].status, this.userInfo.accattach.UserID]);
		},

		computed: {
			...mapState(['orderResult']),
		},

		mounted() {
			this.userInfo = JSON.parse(localStorage.getItem('userInfo'));
			this.pageID = 1;
			this.loadOrderResult([this.pageID - 1, this.orders[this.state][this.switchxz].status, this.userInfo.accattach.UserID]);
		},

		methods: {
			...mapActions(['loadOrderResult', 'loadOrderDetails']),

			changePage(id) {
				this.pageID = id;
				this.loadOrderResult([id - 1, this.orders[this.state][this.switchxz].status, this.userInfo.accattach.UserID]);
			},

			switchTab(ind) {
				this.pageID = 1;
				this.switchxz = ind;
				this.loadOrderResult([this.pageID - 1, this.orders[this.state][this.switchxz].status, this.userInfo.accattach.UserID]);
			},

			async showOrderDetails(row) {
				let address = `${row.RcName} ${row.RcPhone} ${row.RcAddr}`;
				let expressType;
				if (row.OrderType === '订货单') {
					expressType = row.ExpressType === 1 ? '快递物流' : '自提';
				} else {
					expressType = null;
				}
				let pickUpTime = row.PickupTime || '';
				if (pickUpTime) {
					pickUpTime = pickUpTime.replace('T', ' ');
				}
				this.loadOrderDetails([row.Out_Trade_No, row.remark, address, expressType, pickUpTime]);
				this.isDetailsShow = true;
			},

			async hideOrderDetails() {
				this.isDetailsShow = false;
			},

			cancelOrder(row) {
				this.$Modal.confirm({
					title: '提醒',
					content: '确定取消这个订单？',
					onOk: async () => {
						let res = await this.$request.cancelOrder(row.Out_Trade_No);
						if (res) {
							this.pageID = 1;
							this.loadOrderResult([this.pageID - 1, this.orders[this.state][this.switchxz].status, this.userInfo.accattach.UserID]);
							this.$Message.success('取消订单成功');
						}
					},
					onCancel: () => {
						this.$Message.info('订单未取消');
					}
				});
			}
		},
	};
</script>

<style scoped>
	.page-size {
		margin: 15px 0px;
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}

	.MyOrder_header {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding: 15px 0;
	}

	.MyOrder_header > div {
		padding: 5px 15px;
		cursor: pointer;
	}

	.MyOrder_header > div.xz {
		color: #2d8cf0;
		background: #f0faff;
	}
</style>



